<template lang="">
<div style="height:100%;background:#efeee9;">
  <!-- <mt-navbar v-model="selected" value="2">
    <mt-tab-item v-for="(item,index) in datas" :id="item.id"> {{item.name}}</mt-tab-item>

  </mt-navbar> -->
   <ul class="tab-nav normal-box">
            <li v-for="(item,index) in datas"  :class="{active:datas[index].active}">
                <router-link :to="item.path">{{item.tabName}}</router-link>
            </li>
        </ul>
    <router-view></router-view>
  <!-- tab-container -->

 <!--  <transition name="slide-right">
    <router-view class="cover-transition"></router-view>
  </transition> -->
</div>
</template>
<script>
import {
  Navbar,
  TabItem,
  InfiniteScroll
} from 'mint-ui';
import {
  Toast
} from "mint-ui";
Vue.component(Navbar.name, Navbar);
Vue.component(TabItem.name, TabItem);
Vue.use(InfiniteScroll);
export default {
  data() {
    return {
      //selectMarketTag:"1",
      datas: [
        {
          id: "1",
          tabName: "发行市场",
          itemName: "发行",
          active:false,
          path:"/market/marketFinancial"
        },

        {
          id: "2",
          tabName: "优先市场",
          itemName: "优先市场",
          active:false,
           path:"/market/marketFirstJj"
        },
        {
          id: "3",
          tabName: "普通市场",
          itemName: "普通市场",
          active:false,
            path:"/market/marketJj"
        },
        //  {
        //   id: "3",
        //   tabName: "转让",
        //   itemName:"转让"
        // },
      ],
      selected: "1",
      loading: false,
      dataList: [],
      dataListJj: [],
      indexNow: '',
      pars: {
        epsBgn: "",
        epsEnd: "",
        hqzqdm: "",
        industry: "",
        offset: 0,
        pageSize: global.pageSize,
        recNo: 1,
        regCapitalBgn: "",
        regCapitalEnd: "",
        region: "",
        sortFields: "",
        xyscdm: global.scdm,
        xyywlb: global.STKBIZTYPE.PRICE_BUY_APPL,//定价买入业务类别
      }
    }
  },
  created() {
   if(this.$route.name=="market"){
      this.$router.push(this.datas[0].path);
   }else{
    this.$router.push(this.$route.path);
   }
    // if(this.$route.name=="marketFirstJj"){
    //   this.selected="2";
    // }
    // if(this.$route.name=="marketJj"){
    //   this.selected="3";
    // }
  },
  // watch:{
  //   selected:function(){
  //     // if(this.selected=="3"){
  //     //   this.$router.push("/market/marketJj")
  //     // }else if(this.selected=="1"){
  //     //    this.$router.push("/market/marketFinancial")
  //     // }else if(this.selected=="2"){
  //     //   this.$router.push("/market/marketFirstJj")
  //     // }
  //   }
  // },
  computed: {},
  methods: {
  
  }
}
</script>
<style media="screen" scoped >
.financial-box {
  display: -webkit-flex;
  /* Safari */
  display: flex;
}

.financial-box>div {
  flex-grow: 1;
  width: 33.33%;
}

.market-table-header {
  width: 90%;
  margin: 0 auto;
  margin-top: 6px;
  font-size: 12px;
  color: #999;
}

.market-table li {
  padding: 4px;
  border-bottom: 1px solid #eee;
}

.nhsyl-text {
  font-size: 14px;
  color: #999;
}

.mint-tab-container {
  height: 100%;
  padding-bottom: 41px;
}

.my-tab-container2 {
  height: 100%;
}
</style>
<style media="screen">
.mint-tab-container-wrap {
  height: 100%;
}

 ::-webkit-scrollbar {
  width: 0px;
  height: 1px;
}

 ::-webkit-scrollbar-thumb {
  border-radius: 5px;
  -webkit-box-shadow: inset 0 0 5px rgba(0, 0, 0, 0.2);
  background: rgba(0, 0, 0, 0.2);
}
</style>



